<template>
  <div class="view aside">
    <aside class="pickup" id="pickuptree">
      <c-pickup-tree
        :treeOptions="APP.treeOptions"
        @node-click="APP.handleClickAside"
        ref="pickupTree"
      ></c-pickup-tree>
    </aside>
    {{APP.c("APP.dslArray",APP.dslArray)}}
    {{APP.c("APP.model",APP.model)}}
    {{APP.c("APP.activeNames",APP.activeNames)}}
    <main class="main">
      <el-collapse v-model="APP.activeNames">
        <template v-if="APP.pageType===2">
          <el-collapse-item
            :title="dsl.title"
            :name="index"
            v-for="(dsl, index) in APP.dslArray"
            :key="index"
          >
            <c-table
              class="box-card"
              v-model="APP.model"
              :dsl="dsl.rows"
              ref="topTabe"
              :disabled="true"
            />
          </el-collapse-item>
        </template>
      </el-collapse>
    </main>
  </div>
</template>

<script>
import Main from "./Main.js";
import $ from "jquery";

export default {
  name: "FnbpcFkjc",
  mixins: [Main],
  inject: ["APP"],
  mounted() {},
  data() {
    return {
      /* 动态数据 */
      asideTree: []
    };
  },
  methods: {
    /* 完成刷新前的数据检测 */
    async save() {
      try {
        /* 验证表单是否都符合rule */
        if (await this.$refs.topTabe.valid()) {
          this.APP.save();
        } else {
          throw new Error("请按照提示检查输入项");
        }
      } catch (error) {
        this.$message({
          showClose: true,
          message: error.message,
          type: "warning"
        });
      }
    },
    getTitle(fix) {
      return this.APP.model.xm
        ? `${fix}-${this.APP.model.xm}-${this.APP.model.bh}`
        : fix;
    }
  }
};
</script>

<style scoped>
#pickuptree {
  width: 180px;
}
</style>


